body {
  color: #333;
}
a {
  text-decoration: none;
}

.inner {
  width: 65%;
  margin: 0 auto;
}

.button {
  display: block;
  width: 100%;
  height: 60px;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
  border: 0px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.green-button {
  background-color: #06c655;
  color: white;
}

.black-button {
  background-color: #000000;
  color: white;
}

.blue-button {
  background-color: #1977f1;
  color: white;
}
